@import '././core/reset.scss';
@import '././core/variables.scss';

.form__header {
    position: relative;
    padding: 30px 0px 30px 0px;
  }
  .form__header--image {
    width: 201px;
    height: 50px;
    -o-object-fit: contain;
    object-fit: contain;
    display: block;
    margin: auto;
  }
  @media all and (min-width: 768px) {
    .form__header--image {
      margin-left: 40px;
    }
  }